<template lang="pug">
  .show-product.pt-3.is-flex.flex-column

    .container.mb-2.text-left.px-0.valign.justify-content-between.product-nav
      router-link.text-white.valign.justify-content-start.clickable.has-fg-1(:to="{name: 'Services'}")
        i.mdi.is-size-5.mr-2 chevron_left
        | Back to search results

      b-button.btn-circle.ml-2.text-white(
        variant="white"
        v-if="previousProduct.id"
        size="sm"
        v-shortkey="['arrowleft']"
        @shortkey="focusOnProductId(nextProduct.id)"
        :to="{name: 'ShowService', params: {id: previousProduct.id}}"
        @click="focusOnProductId(previousProduct.id)"
      )
        i.mdi chevron_left

      b-button.btn-circle.ml-2.text-white(
        variant="white"
        size="sm"
        v-if="nextProduct.id"
        v-shortkey="['arrowright']"
        @shortkey="focusOnProductId(nextProduct.id)"
        :to="{name: 'ShowService', params: {id: nextProduct.id}}"
        @click="focusOnProductId(nextProduct.id)"
        )
        i.mdi chevron_right


    .header.container.bg-white.is-flex.p-0

      //- PICTURE

      .col.px-0.has-fg-0
        .image(:style="product.attachment ? `background: url(${attachment})` : ''" v-if="product.attachment")

      //- INFO

      .col.pl-4.pr-2.py-5

        h1.h2.is-flex.h4.valign.justify-content-start.text-dark.p-0.mb-4.is-size-3
          | {{product.title}}
          i.flag-icon.ml-3(
            v-for="tagId in languages"
            :class="[`flag-icon-${tagsById[tagId].name.substring(3).toLowerCase()}`]"
          )

        //- HOURLY RATE

        .houry-rate.mr-5
          h3.h6 HOURLY RATE
          p.rate.h3.text-dark
            | {{product.rate.amount}}
            small {{product.rate.currency}}

        div(v-if="experience.length")
          h3.h6 EXPERIENCE
          p.experience.h5.text-dark
            | {{ $t(`tags.${tagsById[experience[0]].kind}.${tagsById[experience[0]].name}`)}}

      .col.pl-2.pr-4.py-5.text-center.valign.flex-column.justify-content-start

        b-button.mb-4(
          block
          variant="primary"
        ) SHOW PHONE NUMBER

        b-button.valign(
          block
          variant="outline-primary"
          v-shortkey="['space']"
          @shortkey="favorite ? removeFavorite(favorite) : addToFavorites()"
          @click="favorite ? removeFavorite(favorite) : addToFavorites()"
        )
          i.mdi.is-size-5.mr-2 {{ favorite ? 'favorite' : 'favorite_border' }}
          | {{ favorite ? 'ADDED' : ' ADD TO FAVORITE' }}

    .w-100.has-fg-1.body
      .container.is-flex.px-0

        //- TAGS

        .col.column-tags.bg-white.has-fg-0.p-4.mt-3
          h2.h5 Info
          p.is-size-6.tag.text-dark.py-3.border-bottom(
            v-for="tagId in tags"
          )
            | {{tagsById[tagId] ? $t(`tags.${tagsById[tagId].kind}.${tagsById[tagId].name}`) : ''}}

        //- DESCRIPTION

        .col.mt-3.column-dexcription.p-4.bg-white
          h2.h5 More about {{product.title}}
          p.is-size-6.text-dark(v-html="product.description")




</template>

<script>
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "ShowProduct",
    components: {
    },
    props: [],
    data() {
      return {
        isLoading: false,
      };
    },
    computed: {
      ...mapGetters([
        'attachmentsById',
        'tagsById',
        'favoritesByModel',
        'user',
        'product',
        'previousProduct',
        'nextProduct',
        'attachments'
      ]),
      attachment() {
        if (!this.product.attachment) {
          return null
        } else if (process.env.NODE_ENV == 'development') {
          return `/uploads/${this.product.attachment}`;
        }
        return `/uploads/attachment/picture/${this.product.id}/${this.product.attachment}`
      },
      favorite() {
        return this.favoritesByModel[`Product-${this.product.id}`]
      },
      languages() {
        return this.product.tags.filter(tagId => this.tagsById[tagId] && this.tagsById[tagId].kind == 'language')
      },
      experience() {
        return this.product.tags.filter(tagId => this.tagsById[tagId] && this.tagsById[tagId].kind == 'experience')
      },
      tags() {
        return this.product.tags.filter(tagId => this.tagsById[tagId] && this.tagsById[tagId].kind != 'language' && this.tagsById[tagId].kind != 'experience')
      }
    },
    methods: {
      ...mapActions(['saveFavorite', 'removeFavorite', 'focusOnProductId']),
      addToFavorites() {
        this.saveFavorite({
          model_type: 'Product',
          model_id: this.product.id
        })
      },
    },
  };
</script>


<style lang="scss">
  @import 'colors';

  .show-product {
    min-height: 100vh;
    width: 100%;
    /*background-color: $purple;*/
    /*background: linear-gradient(75deg,#22263D 0,#342441 25%,#342441 80%,#22263D 100%);*/
    background: linear-gradient(75deg,darken($primary, 20) 0,$primary 25%,darken($primary, 10) 80%,darken($primary, 20) 100%);

    .product-nav a,
    .product-nav .btn {
      height: 2.5rem;
    }

    .image {
      width: 360px;
      height: 360px;
      background-repeat: no-repeat !important;
      background-size: contain !important;
      background-position: center center !important;
    }

    .column-tags {
      min-width: calc(360px - 2rem);
      margin-right: 2rem;
    }

    .info {
      border-radius: 6px 6px 0 0;
    }

    .body {
      background-color: $sweetGray;
    }

    .flag-icon {
      width: 1.5rem;
    }
  }
</style>
